:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
  --frame-default-color: black;
  --frame-selected-color-back: #000;
  --frame-selected-color-front: #f1e500;
  --frame-selected-color-front: #fff53a;
  --frame-dice-width: 50px;
  --frame-dice-height: 80px;
  --font-dice-face: 40px;
  --font-dice-max: 18px;
  --inactive-color: #636363;
  --inactive-color-overlay: rgba(66, 66, 66, .5);
  --selected-color-overlay: rgba(255, 255, 255, .5);
  --border-radius-default: 20%;
  --border-radius-inner: 15%;
  --scale-up: scale(1.1);
  --scale-down: scale(.9);
  --selected-animation-duration: 1.2s;
  --dice-bg-color-bad: #fc9;
  --dice-bg-color-medium: #ffb;
  --dice-bg-color-uncommon: #cfff9f;
  --dice-bg-color-epic: #f6f;
  --dice-bg-color-mythic: #0ff;
  --dice-bg-color-perfect: #0f0;
}

@keyframes animateSelection {
  0% {
    transform: var(--scale-up);
  }

  50% {
    transform: scale(1.13);
  }

  100% {
    transform: var(--scale-up);
  }
}

@keyframes animateSelectionBefore {
  0% {
    box-shadow: 0px 0px 2px 2px var(--frame-default-color), inset 0 0 3px 4px rgba(255, 255, 255, .9);
  }

  50% {
    box-shadow: 0px 0px 3px 2px var(--frame-default-color), inset 0px 0px 7px 6px #fff;
  }

  100% {
    box-shadow: 0px 0px 2px 2px var(--frame-default-color), inset 0 0 3px 4px rgba(255, 255, 255, .9);
  }
}

.button {
  cursor: pointer;
  transition: transform var(--animation-duration) var(--timing-function);
}

.button:hover {
  transform: var(--scale-up);
}

.header:focus, .header:hover {
  overflow: visible;
}

.header {
  grid-gap: 8px;
  background-color: #d1e0db;
  grid-template-columns: repeat(auto-fit, minmax(30px, 60px));
  grid-auto-rows: 30px;
  justify-content: left;
  margin: 11px auto;
  display: grid;
  overflow: hidden;
}

.bag {
  max-width: 1140px;
  grid-template-columns: repeat( auto-fit, minmax(var(--frame-dice-width), var(--frame-dice-width) ) );
  grid-auto-rows: var(--frame-dice-height);
  grid-gap: 8px;
  justify-content: center;
  margin: 11px auto;
  display: grid;
}

.diceBag, .itemBag {
  background-color: #d7d9cc;
}

.item:hover:not(.inactive):before {
  z-index: -1;
}

.item:hover:not(.inactive) {
  transform: var(--scale-up);
  z-index: 2;
}

:not(.inactive).item:before {
  content: "";
  z-index: -1;
  width: 100%;
  height: 100%;
  border: 3px solid;
  border-color: var(--frame-default-color);
  border-radius: var(--border-radius-default);
  background-color: #f6e6b3;
  position: absolute;
  top: 1px;
  right: -4px;
}

.item {
  cursor: pointer;
  z-index: 2;
  border: 3px solid;
  border-color: var(--frame-default-color);
  border-radius: var(--border-radius-default);
  height: 100%;
  width: 100%;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
  background-color: #f6e6b3;
  padding: 0 5px;
  position: relative;
}

.item:not(.inactive) {
  box-shadow: 0 1px 3px 2px rgba(0, 0, 0, .2);
}

.dice:hover:not(.inactive) {
  transform: var(--scale-up);
  z-index: 2;
}

.dice:hover:not(.inactive):before {
  z-index: -1;
}

:not(.inactive).dice:before {
  content: "";
  z-index: -1;
  width: 100%;
  height: 100%;
  border: 3px solid;
  border-color: var(--frame-default-color);
  border-radius: var(--border-radius-default);
  position: absolute;
  top: 1px;
  right: -4px;
}

.dice {
  cursor: pointer;
  z-index: 2;
  border: 3px solid;
  border-color: var(--frame-default-color);
  border-radius: var(--border-radius-default);
  height: 100%;
  width: 100%;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
  padding: 0 5px;
  position: relative;
  top: -2px;
}

.dice:not(.inactive) {
  box-shadow: 0 1px 3px 2px rgba(0, 0, 0, .2);
}

.gallery__link {
  height: 100%;
  text-decoration: none;
  display: block;
}

.dice__max {
  font-size: var(--font-dice-max);
}

.dice__rolled {
  font-size: var(--font-dice-face);
}

.button-end-turn {
  border: 3px solid;
  border-color: var(--frame-default-color);
  border-radius: var(--border-radius-default);
  padding: 0 5px;
}

[bgcolor="--dice-bg-color-bad"], [bgcolor="--dice-bg-color-bad"]:before {
  background-color: var(--dice-bg-color-bad);
}

[bgcolor="--dice-bg-color-medium"], [bgcolor="--dice-bg-color-medium"]:before {
  background-color: var(--dice-bg-color-medium);
}

[bgcolor="--dice-bg-color-uncommon"], [bgcolor="--dice-bg-color-uncommon"]:before {
  background-color: var(--dice-bg-color-uncommon);
}

[bgcolor="--dice-bg-color-epic"], [bgcolor="--dice-bg-color-epic"]:before {
  background-color: var(--dice-bg-color-epic);
}

[bgcolor="--dice-bg-color-mythic"], [bgcolor="--dice-bg-color-mythic"]:before {
  background-color: var(--dice-bg-color-mythic);
}

[bgcolor="--dice-bg-color-perfect"], [bgcolor="--dice-bg-color-perfect"]:before {
  background-color: var(--dice-bg-color-perfect);
}

.inactive {
  transform: var(--scale-down);
}

.inactive:hover {
  transform: var(--scale-down);
  cursor: not-allowed;
}

.inactive:before {
  content: "";
  z-index: 2;
  width: 100%;
  height: 100%;
  background-color: var(--inactive-color-overlay);
  border-radius: var(--border-radius-inner);
  position: absolute;
  right: 0;
  box-shadow: inset -25px -25px 40px -10px rgba(0, 0, 0, .5);
}

.selected {
  border-color: var(--frame-selected-color-back);
  transform: var(--scale-up);
  border: 2px solid !important;
}

.selected:hover {
  transform: var(--scale-up);
}

.selected:before {
  animation: animateSelectionBefore var(--selected-animation-duration) infinite;
  content: "";
  z-index: 2;
  width: 100%;
  height: 100%;
  border-radius: var(--border-radius-inner);
  position: absolute;
  right: 0;
  border: 2px solid !important;
  border-color: var(--frame-selected-color-front) !important;
}

